let itemArray = ['所有人可见', '仅自己可见'];
import uniNavBar from "../../../component/index/uni-nav-bar.vue";
import uploadImg from "../../../component/common/upload-img.vue";
import uniPopup from "../../../component/common/uni-popup.vue"

export default {
	components: {
		uniNavBar,
		uploadImg,
		uniPopup
	},
	onLoad() {
		uni.$on("backPublish", (e) => {
			console.log(e)
			this.topic_id = e.id
			this.hintTopicText = e.title
		})
		this.$nextTick(() => {
			this.$refs['showpopup'].open()
		})
		uni.getStorage({
			key: "userinfo",
			complete: (res) => {
				if (res.data == null || res.data == '') {
					this.isLogin = false
				} else {
					this.isLogin = true
					this.userInfo = JSON.parse(res.data)
				}
			}
		})
		this.getTabList()
	},
	data() {
		return {
			titleText: "所有人可见",
			showSaveDialog: true,
			editText: "",
			imgList: [],
			isLogin: false,
			userInfo: {},
			imgListId: [],
			sortList: [],
			sortArray: [],
			sortIndex: 0,
			hintSortText: "选择分类",
			hintTopicText: "选择话题",
			post_class_id: -1,
			topic_id: -1,
			isopen: 0
		}
	},
	methods: {
		//返回键
		handleBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//标题点击事件
		handleTitleTypeClick() {
			uni.showActionSheet({
				itemList: itemArray,
				success: (res) => {
					this.titleText = itemArray[res.tapIndex],
						this.isopen = res.tapIndex
				}
			});
		},
		//发布按钮
		handlePublish() {
			if (this.editText == null || this.editText == '') {
				return uni.showToast({
					title: "请输入内容",
					icon: "none"
				})
			}
			if (this.imgListId.length <= 0) {
				return uni.showToast({
					title: "请上传图片",
					icon: "none"
				})
			}
			if (this.post_class_id <= 0) {
				return uni.showToast({
					title: "请选择分类",
					icon: "none"
				})
			}
			if (this.topic_id <= 0) {
				return uni.showToast({
					title: "请选择话题",
					icon: "none"
				})
			}
			this.publish()
		},
		//选择图片
		handleSelectImg(imgList) {
			this.imgList = imgList
			this.uploadImg()
		},
		//上传图片
		async uploadImg() {
			for (var i = 0; i < this.imgList.length; i++) {
				uni.showLoading({
					title: "上传中..."
				})
				var upRes = await uni.uploadFile({
					url: this.config.BASE_URL + "image/uploadmore",
					filePath: this.imgList[i],
					name: "imglist[]",
					header: {
						token: this.userInfo.token
					}
				})
				uni.hideLoading()
				const imgIdObj = {
					id: JSON.parse(upRes[1].data).data.list[0].id
				}
				this.imgListId.push(imgIdObj)
			}
			uni.showToast({
				title: "上传成功"
			})
		},
		publish() {
			console.log(this.imgListId)
			this.request({
				url: this.config.BASE_URL + "post/create",
				method: "post",
				data: {
					imagelist: this.imgListId,
					text: this.editText,
					isopen: this.isopen,
					topic_id: this.topic_id,
					post_class_id: this.post_class_id
				},
				header: {
					token: this.userInfo.token
				}
			}).then(res => {
				console.log(res)
			})
		},
		// 关闭弹窗
		handleClosePop() {
			this.$nextTick(() => {
				this.$refs['showpopup'].close()
			})
		},
		//提示窗
		handleShowSaveDialog() {
			uni.showModal({
				content: "是否要保存为草稿",
				cancelText: "不保存",
				confirmText: "保存",
				success: (res) => {
					if (res.confirm) {
						console.log("保存")
					} else {
						console.log("不保存")
					}
					this.showSaveDialog = false
					uni.navigateBack({
						delta: 1
					})
				}
			})
		},
		//获取分类列表数据
		getTabList() {
			this.request({
				url: this.config.BASE_URL + "postclass"
			}).then(result => {
				this.sortList = result.data.list
				this.sortList.forEach(v => {
					this.sortArray.push(v.classname)
				})
			})
		},
		bindPickerChange: function(e) {
			this.sortIndex = e.target.value
			this.hintSortText = this.sortArray[e.target.value]
			this.post_class_id = this.sortList[this.sortIndex].id
		},
		handleSelectTopic() {
			uni.navigateTo({
				url: "/pages/news/topic-sort/topic-sort?type=publish"
			})
		}
	},
	onBackPress() {
		if ((this.editText || this.imgList.length > 0) && this.showSaveDialog) {
			this.handleShowSaveDialog()
			return true;
		}
	}
}
